.bar {
  width: 35px;
  height: 5px;
  background-color: black;
  margin: 0 6px 6px 40px;
  display: block;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.hamburger {
  width: 80px;
  height: 65px;
  display: block;
  cursor: pointer;
  padding-top: 20px;
  position: relative;
  z-index: 1;
  margin-top: 20px;
}

.hamburger-checkbox {
  display: none;
}

.menu {
  position: absolute;
  width: 100%;
  height: 65px;
  top: -100%;
  -webkit-transition: all 0.7s ease;
  -o-transition: all 0.7s ease;
  transition: all 0.7s ease;
  opacity: 0;
  display: block;
}

.nav-menu {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 70px;
  margin-top: 20px;
}

.nav-list {
  width: 35%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.nav-link:hover {
  color: #F80793;
}

.hamburger-checkbox:checked ~ .menu {
  opacity: 1;
  top: 0;
}

.hamburger-checkbox:checked + .hamburger .bar1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 6px);
      -ms-transform: rotate(-45deg) translate(-9px, 6px);
          transform: rotate(-45deg) translate(-9px, 6px);
}

.hamburger-checkbox:checked + .hamburger .bar2 {
  opacity: 0;
}

.hamburger-checkbox:checked + .hamburger .bar3 {
  -webkit-transform: rotate(45deg) translate(-8px, -8px) ;
      -ms-transform: rotate(45deg) translate(-8px, -8px) ;
          transform: rotate(45deg) translate(-8px, -8px) ;
}

@media only screen and (max-width: 700px) {
  .menu {
    height: 100%;
    background-color: rgb(8,2,42);
  }
  .nav-list {
    -ms-flex-direction: column;
        flex-direction: column;
    width: 100%;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin: 0;
    padding: 0;
  }
  .nav-menu {
    height: 50%;
  }
  .nav-link {
    font-size: 4vw;
    line-height: 40px;
    color: white;
  }
  .bar {
    background-color: #F80793;
  }
}

@media only screen and (min-width: 700px) and (max-width: 900px) {
  .nav-link {
    font-size: 1.5vw;
  }
  .nav-list {
    width: 45%;
  }
}



