.footer-container {
  background-color: #EAEDf0;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 10vw;
}

.logo-container {
  width: 9vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.social-image {
  width: 2.5vw;
  height: 2.5vw;
}

@media only screen and (max-width: 500px) {
  .logo-container {
    width: 22vw;
  }
  .footer-container {
    height: 20vw;
  }
  .social-image {
    width: 5.5vw;
    height: 5.5vw;
  }
}



